'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Worker } from '@/lib/types';
import { addWorker, generateId, formatDate } from '@/lib/storage';

export default function NewWorkerPage() {
  const router = useRouter();
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    role: 'labour' as Worker['role'],
    wageRate: '',
  });

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.name.trim()) {
      newErrors.name = 'Name is required';
    }

    if (!formData.phone.trim()) {
      newErrors.phone = 'Phone number is required';
    } else if (!/^\+?[\d\s\-\(\)]+$/.test(formData.phone)) {
      newErrors.phone = 'Please enter a valid phone number';
    }

    if (!formData.wageRate) {
      newErrors.wageRate = 'Wage rate is required';
    } else if (isNaN(Number(formData.wageRate)) || Number(formData.wageRate) <= 0) {
      newErrors.wageRate = 'Please enter a valid wage rate';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setLoading(true);

    try {
      const newWorker: Worker = {
        id: generateId(),
        name: formData.name.trim(),
        phone: formData.phone.trim(),
        role: formData.role,
        wageRate: Number(formData.wageRate),
        isActive: true,
        joinDate: formatDate(new Date()),
      };

      addWorker(newWorker);
      router.push('/workers');
    } catch (error) {
      console.error('Error adding worker:', error);
      setErrors({ submit: 'Failed to add worker. Please try again.' });
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  return (
    <div className="max-w-2xl mx-auto space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Add New Worker</h1>
        <p className="text-gray-600 mt-1">Enter worker information to add them to your workforce</p>
      </div>

      {/* Form */}
      <Card>
        <CardHeader>
          <CardTitle>Worker Information</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Name */}
            <div className="space-y-2">
              <Label htmlFor="name">Full Name *</Label>
              <Input
                id="name"
                type="text"
                value={formData.name}
                onChange={(e) => handleInputChange('name', e.target.value)}
                placeholder="Enter worker's full name"
                className={errors.name ? 'border-red-500' : ''}
              />
              {errors.name && (
                <p className="text-sm text-red-600">{errors.name}</p>
              )}
            </div>

            {/* Phone */}
            <div className="space-y-2">
              <Label htmlFor="phone">Phone Number *</Label>
              <Input
                id="phone"
                type="tel"
                value={formData.phone}
                onChange={(e) => handleInputChange('phone', e.target.value)}
                placeholder="Enter phone number"
                className={errors.phone ? 'border-red-500' : ''}
              />
              {errors.phone && (
                <p className="text-sm text-red-600">{errors.phone}</p>
              )}
            </div>

            {/* Role */}
            <div className="space-y-2">
              <Label htmlFor="role">Role *</Label>
              <select
                id="role"
                value={formData.role}
                onChange={(e) => handleInputChange('role', e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="labour">Labour</option>
                <option value="supervisor">Supervisor</option>
                <option value="manager">Manager</option>
              </select>
            </div>

            {/* Wage Rate */}
            <div className="space-y-2">
              <Label htmlFor="wageRate">Hourly Wage Rate ($) *</Label>
              <Input
                id="wageRate"
                type="number"
                step="0.01"
                min="0"
                value={formData.wageRate}
                onChange={(e) => handleInputChange('wageRate', e.target.value)}
                placeholder="Enter hourly wage rate"
                className={errors.wageRate ? 'border-red-500' : ''}
              />
              {errors.wageRate && (
                <p className="text-sm text-red-600">{errors.wageRate}</p>
              )}
            </div>

            {/* Submit Error */}
            {errors.submit && (
              <div className="p-3 bg-red-50 border border-red-200 rounded-md">
                <p className="text-sm text-red-600">{errors.submit}</p>
              </div>
            )}

            {/* Actions */}
            <div className="flex gap-4 pt-4">
              <Button
                type="submit"
                disabled={loading}
                className="flex-1"
              >
                {loading ? 'Adding Worker...' : 'Add Worker'}
              </Button>
              <Button
                type="button"
                variant="outline"
                onClick={() => router.push('/workers')}
                disabled={loading}
              >
                Cancel
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>

      {/* Info Card */}
      <Card>
        <CardHeader>
          <CardTitle className="text-lg">Information</CardTitle>
        </CardHeader>
        <CardContent className="space-y-2 text-sm text-gray-600">
          <p>• All fields marked with * are required</p>
          <p>• Phone numbers can include country codes and formatting</p>
          <p>• Wage rates should be entered as hourly rates in dollars</p>
          <p>• New workers will be automatically set as active</p>
          <p>• Join date will be set to today's date</p>
        </CardContent>
      </Card>
    </div>
  );
}
